<template>
	<el-container id="app">
		<el-aside style="width: 240px;">
			<div class="sidebar-box">
				<sidebar></sidebar>
			</div>
		</el-aside>
		<el-container>
			<el-header style="display: none;">
				<vHeader></vHeader>
			</el-header>
			<el-main id="content">
				<router-view/>
			</el-main>
		</el-container>
	</el-container>
</template>

<script>
import vHeader from './components/v-header'
import sidebar from './components/sidebar'
export default {
	name: 'App',
	components: {
		vHeader,
		sidebar,
	},
}
</script>

<style lang="less">
	@import "common/style/common";
	#app {
		@sideBarWidth: 240px;
		.sidebar-box {
			position: fixed;
			width: @sideBarWidth;
			height: 100%;
		}

		@import "./common/style/docs";
	}
</style>



